import { useState } from 'react';
import { Button, Modal } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { changeShow } from '../reduxProject/actions/changeShow';
import { logout } from '../reduxProject/actions/updateCustomer';
import Register from './Register';
import icon from '../Images/-01.png';

export default function Footer() {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const showRegister = useSelector((state) => state.loginModalReducer);
    const [showRemoving, setShowRemoving] = useState(false);
    const [isRemoving, setIsRemoving] = useState(false);
    const person = useSelector((state) => state.customerReducer);
    const handleClose = () => setShowRemoving(false);


    return (
        <div className="footer" dir='rtl'>
            <div className="footer-grid">
                <div>
                    <label><input style={{ width: "2vw" }} type='checkbox' />
                               
                            ,
                                </label>
                    <br></br>
                    <br></br>
                    <img src={icon} style={{ width: "3vw" }} />
                </div>
                <div>
                    <a onClick={() => { navigate("about") }} href="#keep-in-touch"> </a>
                    <a href='#' onClick={(e) => { e.preventDefault(); dispatch(changeShow()); }}></a>
                    <a href='#' onClick={(e) => { e.preventDefault(); dispatch(changeShow()); }}></a>
                    <a href='#' onClick={() => setShowRemoving(true)}></a>
                    <a href='#' onClick={() => { navigate("/about") }}></a>
                </div>


                <div>
                    <a href='#' onClick={() => { navigate("pastries/cakesReducer/all") }}><b></b></a>
                    <a href='#' onClick={() => { navigate("pastries/cakesReducer/ ") }}> </a>
                    <a href='#' onClick={() => { navigate("pastries/cakesReducer/ ") }}> </a>
                    <a href='#' onClick={() => { navigate("pastries/cakesReducer/  ") }}>  </a>
                    <a href='#' onClick={() => { navigate("pastries/cakesReducer/  ") }}>  </a>
                </div>
                <div>
                    <a href='#' onClick={() => { navigate("pastries/dessertsReducer/all") }}><b></b></a>
                    <a href='#' onClick={() => { navigate("pastries/dessertsReducer/   ") }}>   </a>
                    <a href='#' onClick={() => { navigate("pastries/dessertsReducer/ ") }}> </a>
                    <a href='#' onClick={() => { navigate("pastries/dessertsReducer/ ") }}> </a>
                    <a href='#' onClick={() => { navigate("pastries/dessertsReducer/") }}></a>
                    <a href='#' onClick={() => { navigate("pastries/dessertsReducer/ ") }}> </a>
                </div>
                <div>
                    <a href='#' onClick={() => { navigate("pastries/cookiesAndsweetsReducer/all") }}><b> </b></a>
                    <a href='#' onClick={() => { navigate("pastries/cookiesAndsweetsReducer/ ") }} > </a>
                    <a href='#' onClick={() => { navigate("pastries/cookiesAndsweetsReducer/") }} ></a>
                    <a href='#' onClick={() => { navigate("pastries/cookiesAndsweetsReducer/  ") }} >  </a>
                    <a href='#' onClick={() => { navigate("pastries/cookiesAndsweetsReducer/ ") }} > </a>
                    <a href='#' onClick={() => { navigate("pastries/dessertsReducer/ ") }}> </a>
                </div>
                <div>
                    <a href='#' onClick={() => navigate("pastries/breadsAndSaltyReducer/all")}><b> </b></a>
                    <a href='#' onClick={() => { navigate("/pastries/chagim/all") }}><b></b></a>
                    <a href='#' onClick={() => { navigate("pastries/chagim/ ") }} > </a>
                    <a href='#' onClick={() => { navigate("pastries/chagim/") }} ></a>
                    <a href='#' onClick={() => { navigate("pastries/chagim/") }} ></a>
                </div>
            </div>
            <hr></hr>
            <div className="footer-bottom">
                <p>        </p>
                <p>Jan 2023</p>
            </div>
            {showRegister && <Register></Register>}
            <Modal className='remove-modal' show={showRemoving} onHide={handleClose} dir='rtl'>
                <Modal.Header closeButton></Modal.Header>
                <Modal.Title ></Modal.Title>

                {!isRemoving && <h5>{person.firstName ? ` ${person.firstName},  / /      ?` :
                    "       "}</h5>}
                {isRemoving && <h5>   </h5>}

                <div style={{ display: "flex", justifyContent: "space-between" }}>
                    {isRemoving ? <Button variant="secondary" onClick={() => { setIsRemoving(false); handleClose(); }}></Button> :
                        <Button variant="secondary" onClick={handleClose}></Button>}
                    {person.firstName && <Button variant="secondary" onClick={() => { dispatch(logout()); setIsRemoving(true) }}></Button>}

                </div>

            </Modal>
        </div>);
};
