import Nav from 'react-bootstrap/Nav';
import { useNavigate } from 'react-router-dom';
import React, { useState, useEffect } from 'react';
import { Container, InputGroup, Navbar } from 'react-bootstrap';
import { NavDropdown } from 'react-bootstrap';
import { AiFillStar } from "react-icons/ai";
import { AiOutlineStar } from "react-icons/ai";
import { BsPerson } from "react-icons/bs";
import { BsFillPersonFill } from "react-icons/bs";
import { AiOutlineShopping } from "react-icons/ai";
import { AiFillShopping } from "react-icons/ai";
import { BsSearch } from "react-icons/bs";
import { FiArrowUp } from "react-icons/fi";
import MovingText from 'react-moving-text'
import { useDispatch, useSelector } from 'react-redux';
import { changeShow, changeShowAd } from '../reduxProject/actions/changeShow';
import logo from '../Images/ -01.png';
import icon from '../Images/-01.png';
import { useRef } from 'react';
import Register from './Register';
import Advertisement from './Advertisement';

export default function AppHeader() {
  //#region   
  const dispatch = useDispatch();
  //#endregion
  const showLogin = useSelector((state) => state.loginModalReducer);
  const showAd = useSelector(state => state.advertisementModalReducer);
  const [showIcon, setShowIcon] = useState(false);
  const customer = useSelector((state) => state.customerReducer);
  const shoppingCart = useSelector((state) => state.shoppingCartReducer);
  const wishlist = useSelector((state) => state.wishlistReducer);
  const navigate = useNavigate();
  const [star, setStar] = useState(AiOutlineStar);
  const [bag, setBag] = useState(AiOutlineShopping);
  const [person, setPerson] = useState(BsPerson);
  const [scrollTop, setScrollTop] = useState(false);
  const [showSubCategories, setShowSubCategories] = useState(false);
  const showDropdown = () => {
    setShowSubCategories(true);
  }
  const hideDropdown = e => {
    setShowSubCategories(false);
  }
  const [currentDropdown, setCurrentDropdown] = useState();

  const valueForSearch = useRef();
  const state = useSelector(state => state);
  const findPastries = () => {
    if (valueForSearch.current.value) {
      navigate(`/pastries/search/${valueForSearch.current.value}`);
      valueForSearch.current.value = "";
    }
  }

  function headerStyle() {
    const header = document.querySelector('.second-navbar');
    if (this.scrollY > 130) {
      header.style.position = "fixed";
      header.style.top = "0";
      header.style.boxShadow = "4px 4px 8px rgba(71, 71, 71, 0.222)";
      setScrollTop(true);
      setShowIcon(true);
    }
    else {
      header.style.position = "relative";
      header.style.top = "auto";
      header.style.boxShadow = "none";
      setScrollTop(false);
      setShowIcon(false);
    }
  }
  window.addEventListener("scroll", headerStyle, false);
  useEffect(() => {
    navigate("/home/");
    window.scrollTo(0, 0);
    const timer = setTimeout(() => {
      dispatch(changeShowAd());
    }, 5000);
    return () => clearTimeout(timer);
  }, []);

  return (
    <>
      <div className='header-wrapper'>
        <div style={{ backgroundColor: "black" }}>
          <MovingText className="moving-text"
            style={{ textAlign: "end", margin: "0", color: "white", height: "3vh", fontSize: "1.8vh" }}
            type="slideOutToLeft"
            duration="50000ms"
            delay="0s"
            direction="normal"
            timing="liner"
            iteration=" infinite"
            fillMode="none">
                * 
          </MovingText>
        </div>
        <div className='fixed-header'>
          <Navbar expand="lg" className='first-navbar'>
            <Container>
              <Nav>
                <Nav.Item>
                  <Nav.Link
                    className='icon-logo'
                    onMouseMove={() => { setPerson(BsFillPersonFill) }}
                    onMouseOut={() => { setPerson(BsPerson) }}
                    onClick={() => dispatch(changeShow())}
                    title="">
                    {person}
                  </Nav.Link>
                  {customer.firstName && <h6> {customer.firstName} </h6>}
                </Nav.Item>

                <Nav.Item style={{ position: "relative" }}>
                  <Nav.Link
                    className='icon-logo'
                    onMouseMove={() => { setBag(AiFillShopping) }}
                    onMouseOut={() => { setBag(AiOutlineShopping) }}
                    onClick={() => { navigate("/shoppingCart") }}
                    title=" ">
                    {bag}
                  </Nav.Link>
                  {(shoppingCart.length > 0) && <div className='qty-circle'>{shoppingCart.length}</div>}
                </Nav.Item>
                <Nav.Item style={{ position: "relative" }}>
                  <Nav.Link
                    className='icon-logo'
                    onMouseMove={() => { setStar(AiFillStar) }}
                    onMouseOut={() => { setStar(AiOutlineStar) }}
                    onClick={() => navigate("/wishlist")}
                    title="  ">
                    {star}
                  </Nav.Link>
                  {(wishlist.length > 0) && <div className='qty-circle'>{wishlist.length}</div>}
                </Nav.Item>
              </Nav>
              {/* <div className='app-logo'><h2> </h2><h5>  </h5></div> */}
              <img src={logo} style={{ width: "14vw", margin: "0 auto" }}></img>
              <Nav.Item>
                <InputGroup dir='ltr'>
                  <InputGroup.Text className='icon-warper'><BsSearch
                    className='search-icon'
                    onClick={(e) => {
                      findPastries();
                    }}
                  />
                  </InputGroup.Text>
                  <input className="form-control mr-sm-2" dir='rtl' ref={valueForSearch} type="text" placeholder="..."
                    onKeyDown={(e) => {
                      if (e.key === "Enter") {
                        findPastries()
                      }
                    }} />
                </InputGroup>

              </Nav.Item>
            </Container>
          </Navbar>
          <hr style={{ color: " rgb(67, 25, 8)", margin: "0" }}></hr>

          <Navbar expand="lg" dir='rtl' className='second-navbar'>
            {showIcon && <img src={icon} style={{ width: "3vw", marginRight: "10vw !important" }} />}

            <Container>
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <Nav style={{ margin: "0 auto" }}>
                  <Nav.Link style={{ fontWeight: "700" }} onClick={() => navigate("/home")}></Nav.Link>
                  <Nav.Link onClick={() => navigate("/about")}></Nav.Link>
                  <Nav.Link onClick={() => navigate("pastries/breadsAndSaltyReducer/all")}> </Nav.Link>
                  <NavDropdown
                    title=""
                    id="collasible-nav-dropdown"
                    show={currentDropdown == "" && showSubCategories}
                    onMouseEnter={() => { setCurrentDropdown(""); showDropdown(); }}
                    onMouseLeave={hideDropdown}>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cakesReducer/ ") }}> </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cakesReducer/ ") }} > </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cakesReducer/  ") }} >  </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cakesReducer/  ") }} >  </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item onClick={() => { navigate("pastries/cakesReducer/all") }}></NavDropdown.Item>
                  </NavDropdown>
                  <NavDropdown
                    title=""
                    id="collasible-nav-dropdown"
                    show={currentDropdown == "" && showSubCategories}
                    onMouseEnter={() => { setCurrentDropdown(""); showDropdown(); }}
                    onMouseLeave={hideDropdown}>
                    <NavDropdown.Item onClick={() => { navigate("pastries/dessertsReducer/   ") }}>   </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/dessertsReducer/ ") }}> </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/dessertsReducer/ ") }}> </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/dessertsReducer/") }}></NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/dessertsReducer/ ") }}> </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item onClick={() => { navigate("pastries/dessertsReducer/all") }}></NavDropdown.Item>
                  </NavDropdown>
                  <NavDropdown
                    title=" "
                    id="collasible-nav-dropdown"
                    show={currentDropdown == " " && showSubCategories}
                    onMouseEnter={() => { setCurrentDropdown(" "); showDropdown(); }}
                    onMouseLeave={hideDropdown}>
                    <NavDropdown.Item onClick={() => { navigate("pastries/dessertsReducer/   ") }}>   </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cookiesAndsweetsReducer/ ") }} > </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cookiesAndsweetsReducer/") }} ></NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cookiesAndsweetsReducer/  ") }} >  </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/cookiesAndsweetsReducer/ ") }} > </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item onClick={() => { navigate("pastries/cookiesAndsweetsReducer/all") }} ></NavDropdown.Item>
                  </NavDropdown>
                  <NavDropdown title=""
                    id="collasible-nav-dropdown"
                    show={currentDropdown == "" && showSubCategories}
                    onMouseEnter={() => { setCurrentDropdown(""); showDropdown(); }}
                    onMouseLeave={hideDropdown}>
                    <NavDropdown.Item onClick={() => { navigate("pastries/chagim/ ") }} > </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/chagim/") }} ></NavDropdown.Item>
                    <NavDropdown.Item onClick={() => { navigate("pastries/chagim/") }} ></NavDropdown.Item>
                  </NavDropdown>
                </Nav>
              </Navbar.Collapse>
            </Container>
          </Navbar>
        </div>
        {showLogin && <Register></Register>}
        {showAd && <Advertisement></Advertisement>}
      </div>
      {scrollTop && <button className='scroll-button' onClick={() => window.scrollTo(0, 0)} title=" "><FiArrowUp></FiArrowUp></button>}
    </>

  );
}
